




























import { Vue, Component, Watch, Model, Emit} from 'vue-property-decorator'
import {
  State,
  Action
} from 'vuex-class'
@Component
export default class AnimationForm extends Vue {
  animation={
    time: 1000,
    delay: 0,
    name: 'bounceIn',
    count: 3,
    function: 'ease',
    interval: 1000
  }

  functionList=[{
    value: 'ease',
    name: ''
  }]

  animationList=[
    {
      value: 'bounceIn',
      name: '',
      icon: 'star'
    },
    {
      value: 'bounceOut',
      name: '',
      icon: 'star'
    },
    {
      value: 'swing',
      name: 'swing',
      icon: 'star'
    }
  ]

  @Model('change') originData:any

  @Watch('originData')
  onElementChanged ():void {
    this.animation = this.originData
  }

  handlerChange () {
    this.$emit('change', this.animation)
  }

  mounted ():void {
    this.animation = this.originData
  }
}



