








































































import {Vue, Component} from 'vue-property-decorator'
import {
  State,
  Getter,
  Action
} from 'vuex-class'
@Component
export default class WorkHeader extends Vue {
  @Getter curElement
  @Action actAddElement
  handlerAddRichText () {
    let newElement = {
      type: "richText",
      extra: {
        content: "<p></p>"
      }

    }
    this.actAddElement(newElement)
  }
}



